import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Attachment } from 'src/app/models/attachment.model';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

import { FileUploader, FileItem } from 'ng2-file-upload';
import { AuthService } from 'src/app/services/auth.service';
import { NotificationService } from 'src/app/services/notification.service';
import { stringify } from '@angular/compiler/src/util';

@Component({
  selector: 'app-attachments-uploader',
  templateUrl: './attachments-uploader.component.html',
  styleUrls: ['./attachments-uploader.component.scss']
})
export class AttachmentsUploaderComponent implements OnInit {
  public attachments: Attachment[];

  public uploader: FileUploader;
  public hasBaseDropZoneOver: boolean;
  public hasAnotherDropZoneOver: boolean;

  public get value(): number[] {
    return this.attachments ? this.attachments.map(a => Number.parseInt(a.id.toString(), 10)) : null;
  }

  @Output() valueChange: EventEmitter<number[]> = new EventEmitter();

  constructor(private auth: AuthService, private notifier: NotificationService) {
  }

  public ngOnInit(): void {
    this.attachments = [];
    this.initializeUploader();
  }

  private initializeUploader(): void {
    this.uploader = new FileUploader({
      url: environment.prepareUrl('attachmentUploadUrl'),
      authToken: 'Bearer ' + this.auth.appUser.token,
      allowedFileType: ['image', 'video'],
      autoUpload: false,
      removeAfterUpload: true,
      maxFileSize: 10 * 1024 * 1024, // 10 MB
    });

    this.hasBaseDropZoneOver = false;
    this.hasAnotherDropZoneOver = false;

    this.uploader.onBuildItemForm = (file: FileItem, form) => {
      file.withCredentials = false;
      form.append('type', file.file.type.split('/')[0]);
    };

    this.uploader.onSuccessItem = (item: FileItem, response: string, status, headers) => {
      const res: Attachment = JSON.parse(response);

      const attachment = new Attachment(res.id, res.type, res.url, res.name);
      this.attachments.push(attachment);

      // console.log(this.attachments);
      this.notifier.success('Attachment uploaded successfully!', 'Uploader');
      this.valueChange.next(this.value);
    };

    this.uploader.onErrorItem = (item: FileItem, response: string) => {
      this.notifier.error('Failed to upload attachment', 'Uploader');
      // console.log(response);
    };
  }

  public fileOverBase(e: any): void {
    this.hasBaseDropZoneOver = e;
  }

  public fileOverAnother(e: any): void {
    this.hasAnotherDropZoneOver = e;
  }

  public delete(a: Attachment): void {
    this.attachments.splice(this.attachments.indexOf(a), 1);
    this.valueChange.next(this.value);
  }
}
